ection: column\n  align-items: center\n  font-family: $ff-primary\n  background-image: url('/src/assets/images/background.jpg')\n  background-position: center center\n  background-attachment: fixed\n  background-repeat: no-repeat\n  -webkit-background-size: cover\n  -moz-background-size: cover\n  -o-background-size: cover\n  background-size: cover\n\nheader\n  min-width: 100vw\n  display: flex\n  justify-content: center\n  align-items: center\n  padding: 15px 0px 0px 0px\n  background-color: transparent\n  font-size: 1rem\n\n.logo\n  display: flex\n  gap: 15px\n  color: $c-header\n  font-family: $ff-logo\n  font-size: 1.85em\n\nnav\n  display: flex\n  flex-direction: column\n  justify-content: center\n  align-items: center\n  gap: 5px\n  background-color: transparent\n  padding: 15px 15px\n  font-size: 1rem\n\n  form\n    display: flex\n    justify-content: center\n    gap: 10px\n    align-items: center\n\n    input\n      font-size: .8em\n      background-color: $bgc-nav\n      color: $c-input\n      font-weight: $fw-medium\n      border-radius: $border-radius\n      border: 2px solid $border-input\n      text-indent: 6px\n      outline: none\n\n    input:focus\n      border: 2px solid $outline-input\n\n    button\n      font-size: .8em\n      font-weight: $fw-semibold\n      padding: 1px 10px\n      background-color: $bgc-search-btn\n      color: $c-search-btn\n      border: 2px solid $border-search-btn\n      border-radius: $border-radius\n      transition-duration: 400ms\n\n    button:hover\n      cursor: pointer\n\n    button:active\n      border: 2px solid $border-search-btn-active\n\n  .active-error\n    width: fit-content\n    padding: 5px 10px\n    text-align: center\n    font-size: .54em\n    font-weight: $fw-regular\n    color: $c-error-message\n    background: rgba(0, 0, 0, 0.4)\n    border-radius: 10px\n\n  .inactive-error\n    display: unset\n    padding: 0\n\n.hide\n  display: none\n\nmain\n  display: block\n  font-size: 1rem\n  width: 100vw\n  padding: 0 15px\n  min-width: 100vw\n  color: $c-main\n  font-weight: $fw-medium\n  font-size: 1rem\n\n  .current-weather\n    display: flex\n    justify-content: space-around\n\n    .current-weather-left\n      display: flex\n      flex-direction: column\n      gap: 10px\n      [data-name], [data-date-today]\n        font-size: .9em\n\n      [data-weather-description]\n        font-size: 2em\n\n      [data-main-temp]\n        font-size: 3em\n\n      .name-date-container\n        display: flex\n        flex-direction: column\n        gap: 3px\n\n    .current-weather-right\n      justify-self: right\n      display: flex\n      flex-direction: column\n      gap: 10px\n      font-size: 2em\n\n      .current-weather-right-container\n        display: flex\n        flex-direction: column\n\n      .current-weather-right-title\n        font-size: .35em\n        font-weight: $fw-light\n        padding-bottom: 2px\n\n      [data-main-feels_like]\n        min-width: fit-content\n\n      .humidity-container,.feels-like-container, .wind-speed-container\n        display: flex\n        gap: 5px\n\n      .wind-speed-symbol-container\n        display: flex\n        align-items: flex-end\n        flex-direction: row\n\n        [data-wind-speed-symbol]\n          padding-bottom: .2em\n          font-size: .5em\n\n.disable-select\n  user-select: none // supported by Chrome and Opera\n  -webkit-user-select: none // Safari\n  -khtml-user-select: none // Konqueror HTML\n  -moz-user-select: none // Firefox\n  -ms-user-select: none // Internet Explorer/Edge\n\n@media only screen and (min-width: 320px)\n  header\n    font-size: 1.2rem\n\n  nav\n    font-size: 1.2rem\n\n@media only screen and (min-width: 480px)\n  header\n    font-size: 1.5rem\n\n  nav\n    font-size: 1.2rem\n\n  main\n    font-size: 1.2rem\n\n@media only screen and (min-width: 796px)\n  header\n    font-size: 2.5rem\n\n  nav\n    font-size: 2rem\n\n  main\n    font-size: 1.5rem\n"],"sourceRoot":""}]);
